export default {
  namespaced: true,
  state: {
    // 语言
    value: '' // medium small mini
  },
  actions: {
    /**
     * @description 设置尺寸
     * @param {Object} state vuex state
     * @param {String} size 尺寸
     */
    set ({ state, dispatch }, lang) {
      return new Promise(async resolve => {
        // store 赋值
        state.lang = lang
        // 持久化
        await dispatch({
          path: 'lang.value',
          value: state.lang,
          user: true
        }, { root: true })
        // end
        resolve()
      })
    },
    /**
     * @description 从持久化数据读取尺寸设置
     * @param {Object} state vuex state
     */
    // load ({ state, dispatch }) {
    //   return new Promise(async resolve => {
    //     // store 赋值
    //     state.lang = await dispatch('db/get', {
    //       dbName: 'sys',
    //       path: 'lang',
    //       defaultValue: 'df',
    //       user: true
    //     }, { root: true })
    //     // end
    //     resolve()
    //   })
    // }
  }
}
