<template>
  <el-row class="home white-back">
    <el-row type="flex" justify="center">
      <h2>弘扬中国社会主义核心价值观</h2>
    </el-row>
    <el-row class="topTitle">
      <span>富强</span>
      <span>民主</span>
      <span>文明</span>
      <span>和谐</span>
      <span>自由</span>
      <span>平等</span>
      <span>公正</span>
      <span>法治</span>
      <span>爱国</span>
      <span>敬业</span>
      <span>诚信</span>
      <span>友善</span>
    </el-row>
    <el-row>
      <h2 class="pl-40">每日一文</h2>
      <el-row v-loading="loading">
        <h3 class="text-center">{{newArticle.title}}</h3>
        <el-row class="content" v-html="newArticle.content"></el-row>
        <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
          <el-row type="flex" align="middle">
            <span>作者: </span>
            <el-link type="success"
                     :href="linkTo(newArticle.author)"
                     target="_blank">
              <span class="font-size--medium">{{newArticle.author}}</span>
            </el-link>
          </el-row>
          <el-row class="ptb-10" v-if="newArticle.date">{{newArticle.date.curr}}</el-row>
        </el-row>
      </el-row>
      <el-row v-if="showMore" v-loading="moreLoading">
        <el-row v-for="article in articles" :key="article.wc">
          <h3 class="text-center">{{article.title}}</h3>
          <el-row class="content" v-html="article.content"></el-row>
          <el-row type="flex" style="flex-direction: column;" justify="space-around" align="bottom">
            <el-row type="flex" align="middle">
              <span>作者: </span>
              <el-link type="success"
                       :href="linkTo(article.author)"
                       target="_blank">
                <span class="font-size--medium">{{article.author}}</span>
              </el-link>
            </el-row>
            <el-row class="ptb-10">{{article.date.curr}}</el-row>
          </el-row>
        </el-row>
      </el-row>
      <el-row class="readMore" v-loading="true">
        <!--<el-button @click="readMore">下一篇</el-button>-->
      </el-row>
    </el-row>
  </el-row>
</template>
<script>
import { getArticleDate, getArticleToday, getArticleRandom } from '@/api/oneArticle'

export default {
  name: 'home',
  data () {
    return {
      msg: '我是首页',
      loading: false,
      newArticle: {},
      showMore: false,
      moreLoading: false,
      articles: []
    }
  },
  mounted () {
    this.getArticle()
    this.$inView('.readMore').on('enter', () => {
      this.readMore()
    }).on('exit', ()=>{})
  },
  computed: {},
  methods: {
    getArticle () {
      this.loading = true
      getArticleToday({
        param: {}
      }).then(res => {
        this.loading = false
        this.newArticle = res.data
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    },
    linkTo (val) {
      return `http://www.baidu.com/s?wd=${val}`
    },
    readMore () {
      this.showMore = true
      getArticleRandom({
        param: {}
      }).then(res => {
        this.articles.push(res.data)
      }).catch(err => {
        this.moreLoading = false
        console.log('getArticleRandom err', err)
      })
    },
    getOneDayArticle () {
      getArticleDate({
        param: {
          date: '20190425'
        }
      }).then(res => {
        console.log(res)
      }).catch(err => {
        console.log('getArticleDate err', err)
      })
    }
  }
}
</script>
<style lang="scss">
.home {

  .topTitle {
    display: flex;
    justify-content: space-around;

    span {
      margin: 10px;
      width: 40px;
    }
  }

  .content {
    padding: 10px 40px;

    p {
      text-indent: 2em;
    }
  }

  .readMore {
    height: 80px;
  }
}

@media screen and (max-device-width: 400px) {
  // 真实宽度小于400px (非web端 窗口大小)
  .topTitle {
    flex-wrap: wrap;
  }
}
</style>
